<template>
    <div>
        <section class="c-bell">
            <button class="c-bell__button"> <i class="fas fa-bell"></i> </button>
            <div class="c-bell__list">
                 <a href="" v-for="notification in notifications">
                     {{ notification.data.document.title }}
                 </a>
            </div>
            <span @click="makeAsRead"> {{ unreadNotifications.length }} </span>
        </section>
    </div>
</template>


<script>
    export default {
        data: () => ({
            notifications: []
        }),
        props: ['user'],
        methods: {
            makeAsRead(){

                axios.get("/bell/mark_all_reading/"+this.user.id).then( response => {
                    console.log(response);
                });

            }
        },
        computed: {
            unreadNotifications(){
                return this.notifications.filter(notification => {
                    return notification.read_at == null;
                });
            }
        },
        created() {
            this.notifications = window.user.json.notifications;
            console.log('user', window.user.json.notifications);
            console.log('user_id', this.user.id);

            /*================================================================
            =
            =  1. 푸쉬 메시지 받는 부분
            =
            =================================================================*/
            Echo.channel('bell.approval').listen('BellEvent', (event) => {

                var message = event.message;

                var arr = Object.values(event.users);


                for (var i=0; i<arr.length; i++)
                {

                    if( arr[i]['id'] == this.user.id )
                    {
                        toast.fire({
                            title: message,
                            imageUrl: 'https://unsplash.it/400/200',
                            imageWidth: 400,
                            imageHeight: 200,
                            imageAlt: 'Custom image'
                        })
                    }
                    else
                    {
                        //alert("잘못전달");
                    }

                }

            });
            /*================================================================
            =
            =  2. DBNotication Broadcast 받는 부분
            =
            =================================================================*/
            /*
            Echo.private('App.User.' + this.user.id)
                .notification((notification) => {
                    console.log("_______notifi___________");
                    console.log(notification.type);
                });
            */
        }
    }
</script>





<style scoped>
    .c-bell {
        position: relative;
        display: inline-block;
    }

    .c-bell:hover .c-bell__list {
        display: block;
    }

    .c-bell__button {
        padding: 7px 16px;
        background-color: #0063B1;
        font-size: 16px;
        border:none;
        border-radius: 5px;
        cursor: pointer;
    }

    .c-bell__button i {
        color:white;
    }

    .c-bell__list {
        display: none;
        position: absolute;
        left:-100px;
        min-width: 160px;
        background-color: #bbbbbb;
    }

    .c-bell__list > a {
        padding: 12px 16px;
        color:black;
        display: block;
        text-align: left;
    }

    .c-bell__list a:hover {
        background-color: gray;
    }

    .c-bell span {
        cursor: pointer;
        font-size: 18px;
        font-weight: bold;
        color:white;
    }
</style>